// ** React Imports
import { Fragment, useState, useEffect } from 'react'

// ** Store & Actions
import { getLicenseData } from '../store'
import { useDispatch, useSelector } from 'react-redux'
// ** Third Party Components
import ReactPaginate from 'react-paginate'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'

import {
  Badge,
  Row,
  Col,
  Card,
  Input,
  Button,
  Table,
  Modal,
  ModalHeader,
  ModalBody
} from 'reactstrap'
// ** Styles

import '@styles/react/libs/react-select/_react-select.scss'
import moment from 'moment'

// ** Table Header
const CustomHeader = ({ handlePerPage, rowsPerPage }) => {
  
  return (
    <div className='d-flex flex-wrap flex-md-nowrap p-2 p-md-1 my-75'>

      <Col className='col-12 col-md-6 d-flex align-items-center px-1'>
        <div className='d-flex align-items-center w-100'>
          <label htmlFor='rows-per-page'>Show</label>
          <Input
            className='mx-50'
            type='select'
            id='rows-per-page'
            value={rowsPerPage}
            onChange={handlePerPage}
            style={{ width: '5rem' }}
          >
            <option value='10'>10</option>
            <option value='25'>25</option>
            <option value='50'>50</option>
          </Input>
          <label htmlFor='rows-per-page'>Entries</label>
        </div>

      </Col>

    </div>
  )
}

const DigitalSignatureList = () => {
  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector(state => { return state.digitalsignature })

  // ** States
  const [sort] = useState('desc')
  const [searchTerm, setSearchTerm] = useState('')
  const [currentPage, setCurrentPage] = useState(1)
  const [sortColumn] = useState('createdon')
  const [rowsPerPage, setRowsPerPage] = useState(25)
  const [currentStatus, setCurrentStatus] = useState({ id: '', name: 'Select Status' })
  const [currentLocation] = useState({ id: '', name: 'All' })

  const statusArrOptions = [
    { id: 0, name: 'Select All' },
    { id: 1, name: "Active" },
    { id: 2, name: "Inactive" },
    { id: 3, name: "Expired" }
  ]

  const dateFormat = (value) => {
    if (value.length === 13) {
      return moment(value, 'x').format("MMM DD, YYYY")
    } else {
      return moment.unix(value).format("MMM DD, YYYY")
    }

  }

  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  // ** Get data on mount
  useEffect(async () => {
    setBlock(true)
    await dispatch(
      getLicenseData({
        sort,
        sortColumn,
        q: searchTerm,
        page: currentPage,
        perPage: rowsPerPage,
        status: currentStatus.id,
        location: currentLocation.id
      })
    )
    setBlock(false)
  }, [dispatch])

  // ** Function in get data on page change
  const handlePagination = async page => {
    setBlock(true)
    await dispatch(
      getLicenseData({
        sort,
        sortColumn,
        q: searchTerm,
        perPage: rowsPerPage,
        page: page.selected + 1,
        status: currentStatus.id,
        location: currentLocation.id
      })
    )
    setBlock(false)
    setCurrentPage(page.selected + 1)
  }

  // ** Function in get data on rows per page
  const handlePerPage = async e => {
    const value = parseInt(e.currentTarget.value)
    setBlock(true)
    await dispatch(
      getLicenseData({
        sort,
        sortColumn,
        q: searchTerm,
        perPage: value,
        page: currentPage,
        status: currentStatus.id,
        location: currentLocation.id
      })
    )
    setRowsPerPage(value)
    setBlock(false)
  }

  // ** Function in get data on search query change
  const handleFilter = async val => {
    setSearchTerm(val)
    setBlock(true)
    await dispatch(
      getLicenseData({
        sort,
        q: val,
        sortColumn,
        page: currentPage,
        perPage: rowsPerPage,
        status: currentStatus.id,
        location: currentLocation.id
      })
    )
    setBlock(false)
  }

  const handleStatus = async val => {
    setCurrentStatus(val)
    setBlock(true)
    await dispatch(
      getLicenseData({
        sort,
        q: searchTerm,
        sortColumn,
        page: currentPage,
        perPage: rowsPerPage,
        status: val.id,
        location: currentLocation.id
      })
    )
    setBlock(false)
  }


  // ** Custom Pagination
  const CustomPagination = () => {
    const count = Number(Math.ceil(store.total / rowsPerPage))

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName='active'
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={page => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={'pagination react-paginate justify-content-end my-2 pe-1'}
      />
    )
  }

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      status: currentStatus.id,
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })

    if (store.data.length > 0) {
      return store.data
    } else if (store.data.length === 0 && isFiltered) {
      return []
    } else {
      return store.allData.slice(0, rowsPerPage)
    }
  }

  return (
    <Fragment>
      <UILoader blocking={block} loader={<Loader />}>
        <Card className='overflow-hidden'>
          <Row>
            <CustomHeader
              searchTerm={searchTerm}
              rowsPerPage={rowsPerPage}
              handleFilter={handleFilter}
              handlePerPage={handlePerPage}
              statusArrOptions={statusArrOptions}
              currentStatus={currentStatus}
              handleStatus={handleStatus}              
            />

            <Table className='text-nowrap text-center border-bottom' responsive>
              <thead>
                <tr>
                  <th className='text-start'>Name</th>
                  <th>Number</th>
                  <th>Start Date</th>
                  <th>Expiry Date</th>
                  <th>Files</th>
                </tr>
              </thead>
              <tbody>
                {dataToRender().map((item, index) => {
                  return (
                    <tr key={index}>
                      <td className='text-start'>{item.name}</td>
                      <td>
                        {item.licenseno}
                      </td>
                      <td>
                        {item.issueddate !== undefined ? dateFormat(item.issueddate) : ''}
                      </td>
                      <td>
                        {item.expirydate > 0 ? dateFormat(item.expirydate) : ''}
                      </td>
                      <td className='text-start'>
                        {
                          item.files?.map((file, k) => {
                            return (
                              <Badge key={k} className='ms-25' color='primary' >
                                <a target='_blank' className='file-name text-overflow-ellipsis' href={`http://docs.google.com/fileview?id=${file.driveid}&hl=en&pid=explorer&efh=false&a=v&chrome=false&embedded=true`}>{file.originalfilename.split('.')[0].length > 1 ? `${file.originalfilename.split('.')[0].substr(0, 10)}.${file.extension}` : file.originalfilename}</a>
                              </Badge>
                            )
                          })
                        }
                      </td>
                    </tr>
                  )
                })}
              </tbody>
            </Table>
            <CustomPagination />
          </Row>
        </Card>
      </UILoader>
    </Fragment>
  )
}

export default DigitalSignatureList